<template>
  <div class="log-refreshlog">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="log_refreshlog_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
        <el-button type="danger" size="mini" @click="exportLog(selectData)">导出</el-button>
      </template>
      <!-- 当前版本 -->
      <template v-slot:old_version_head="props">
        <el-input
          v-model="params.old_version"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 更新后版本 -->
      <template v-slot:new_version_head="props">
        <el-input
          v-model="params.new_version"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 对应机器 -->
      <template v-slot:ip_head="props">
        <el-input v-model="params.ip" :maxLength="20" size="mini" placeholder="请输入内容" @keyup.enter.native="search"></el-input>
      </template>
      <!-- 更新包类型 -->
      <template slot="type" slot-scope="props">{{props.data.row.type == 1 ? "客户端" : "服务端"}}</template>
      <template v-slot:type_head="props">
        <el-select v-model="params.type" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.pending"></span>正在下载
        </div>
        <div v-else-if="props.data.row.status == 2">
          <span v-html="icon.stateIcon.success"></span>下载成功
        </div>
        <div v-else-if="props.data.row.status == 3">
          <span v-html="icon.stateIcon.error"></span>下载失败
        </div>
        <div v-else-if="props.data.row.status == 4">
          <span v-html="icon.stateIcon.success"></span>更新成功
        </div>
        <div v-else-if="props.data.row.status == 5">
          <span v-html="icon.stateIcon.error"></span>更新失败
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 更新说明 -->
      <template v-slot:desc_head="props">
        <el-input
          v-model="params.desc"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 更新时间 -->
      <template v-slot:ctime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          v-model="params.ctime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      url: "/updatelog/list",
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "当前版本",
          sortable: true,
          prop: "old_version",
          second_head: {
            width: "150px",
            prop: "old_version",
            templateHeadName: "old_version_head"
          }
        },
        {
          label: "更新后版本",
          sortable: true,
          prop: "new_version",
          second_head: {
            width: "150px",
            prop: "new_version",
            templateHeadName: "new_version_head"
          }
        },
        {
          label: "对应机器",
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          sortable: true,
          second_head: {
            width: "150px",
            templateHeadName: "ip_head",
            prop: "ip"
          }
        },
        {
          label: "更新包类型",
          prop: "type",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "type",
            width: "150px",
            templateHeadName: "type_head",
            prop: "type"
          }
        },
        {
          label: "更新状态",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            width: "150px",
            templateHeadName: "status_head",
            prop: "status"
          }
        },
        {
          label: "更新说明",
          prop: "desc",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "desc_head",
            prop: "desc"
          }
        },
        {
          label: "更新时间",
          prop: "ctime",
          sortable: true,
          second_head: {
            width: "200px",
            templateHeadName: "ctime_head",
            prop: "ctime"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        old_version: "",
        new_version: "",
        ip: "",
        type: "",
        status: "",
        desc: "",
        ctime: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        type: [
          {
            id: 1,
            name: "客户端"
          },
          {
            id: 2,
            name: "服务端"
          }
        ],
        status: [
          {
            id: 1,
            name: "正在下载"
          },
          {
            id: 2,
            name: "下载成功"
          },
          {
            id: 3,
            name: "下载失败"
          },
          {
            id: 4,
            name: "更新成功"
          },
          {
            id: 5,
            name: "更新失败"
          }
        ]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      selectData:[]
    };
  },
  methods: {
    reload() {
      this.$refs.log_refreshlog_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select
    },
    exportLog(array) {
      if(this.$tools.isArrayNull(array)){
        this.$message({
          type:"error",
          message:"请选择要导出的日志选项!"
        })
        return
      }
      let str = "序号,当前版本,更新后版本,对应机器,更新包类型,更新状态,更新说明,更新时间,\n"
      var inum = 0
      for(let item of array) {
        inum ++ 
        let type = item.type == 1 ? '客户端' : "服务端"
         let status = ""
        switch(item.status) {
          case 1:
            status = "正在下载"
            break;
          case 2:
            status = "下载成功"
            break;
          case 3:
            status = "下载失败"
            break;
          case 4:
            status = "更新成功"
            break;
          case 5:
            status = "更新失败"
            break;
        }
        str += `${inum},${item.old_version},${item.new_version},${item.ip},${type},${status},${item.desc},${item.ctime},\n`
      }
      this.$tools.exportSCV(str,"更新日志")
    }
  }
};
</script>


